import React from "react";
import { Field, reduxForm } from "redux-form";
import classesCartUser from "../CartUser.module.css";
import { Contact } from "../CartUser";
import { createFild, Input, Textarea } from "../../../common/FormControls/FormsConstrols";



const ProfileDataForm = ({ handleSubmit, profile, error }) => {
    return (<form onSubmit={handleSubmit}>
        {/* <Field name="form1" component="input" /> */}
        <div className={classesCartUser.cart__content}>
            <div className={classesCartUser.dataBritch}>
                <span> :</span>
                {createFild(classesCartUser.Name, "text", " ", Input, "fullName", [])}
            </div>
            <div className={classesCartUser.dataBritch}>
                 :
                {/* {createFild(classesCartUser.dataBritch, "checkbox", props.profile.lookingForAJob ? "" : "", Input, "lookingForAJob", [])} */}
                {createFild(classesCartUser.dataBritch, "checkbox", null, Input, "lookingForAJob", [])}

            </div>
            <div className={classesCartUser.City}>
                 :
                {createFild(classesCartUser.City, "text", " ", Textarea, "lookingForAJobDescription", [])}
            </div>
            <div className={classesCartUser.Education}>
                 :
                {createFild(classesCartUser.Education, "text", " ", Textarea, "aboutMe", [])}
            </div>
            <div className={classesCartUser.WebSite}> :
                {Object.keys(profile.contacts).map((key, index) => {
                    return (<div className={classesCartUser.WebSite}>
                        {key}: {createFild(classesCartUser.contact, "text", key, Textarea, "contacts." + key, [])}
                    </div>)
                }
                )}</div>

                {error}
            <div> <button> </button></div>
        </div>
    </form>)
};

// const ProfileDataFormReduxForm = reduxForm({ form: "profileData" })(ProfileDataForm);

// export default ProfileDataFormReduxForm;
export default reduxForm({
    form: "profileData",
    enableReinitialize: true
})(ProfileDataForm);
