import { stopSubmit } from "redux-form";
import { userAPI, profileAPI } from "../api/api";

const ADD_POST = "ADD-POST";
const UPDATE_NEW_POST_TEXT = "UPDATE-NEW-POST-TEXT";
const INCREASE_LIKE = "INCREASE-LIKE";
const SET_USER_PROFILE = "SET_USER_PROFILE";

const SET_USER_STATUS = "SET_USER_STATUS";

const DELETE_POST = "DELETE_POST";

const SAVE_FOTO_SUCCESS = "/profileReducer/SAVE_FOTO_SUCCESS";
const SAVE_PROFILE = "/profileReducer/SAVE_PROFILE";

const initialState = {
  messagesDataProfile: [
    { id: 1, text: "   ", like: 10 },
    { id: 2, text: "     ", like: 32 },
    { id: 3, text: "    ", like: 322 },
    { id: 4, text: "      ", like: 12 },
    { id: 5, text: "   ", like: 44 },
    { id: 6, text: "      ", like: 12 },
    { id: 7, text: "    ", like: 10 },
  ],
  profile: null,
  _status: "",
};

const profileReducer = (state = initialState, action) => {
  switch (action.type) {
    case ADD_POST: {
      let newMessagesDataProfile = {
        id: state.messagesDataProfile.length + 1,
        text: action.newMyPost,
        like: 0,
      };
      return {
        ...state,
        messagesDataProfile: [
          ...state.messagesDataProfile,
          newMessagesDataProfile,
        ],
      };
    }
    case UPDATE_NEW_POST_TEXT: {
      return {
        ...state,
        newPostText: action.newText,
      };
    }
    case INCREASE_LIKE: {
      let stateCopy = { ...state };
      stateCopy.messagesDataProfile = [...state.messagesDataProfile];
      stateCopy.messagesDataProfile[action.id - 1].like++;
      return {
        ...state,
        messagesDataProfile: [...stateCopy.messagesDataProfile],
      };
    }
    case SET_USER_PROFILE: {
      return {
        ...state,
        profile: action.profile,
      };
    }
    case SET_USER_STATUS: {
      return {
        ...state,
        _status: action.status,
      };
    }
    case DELETE_POST: {
      return {
        ...state,
        messagesDataProfile: state.messagesDataProfile.filter(
          (p) => p.id !== action.postId
        ),
      };
    }
    case SAVE_FOTO_SUCCESS: {
      return {
        ...state,
        profile: { ...state.profile, photos: action.photos },
      };
    }
    case SAVE_PROFILE: {
      return {
        ...state,
        profile: {
          ...state.profile,
          aboutMe: action.dataForm.aboutMe,
          fullName: action.dataForm.fullName,
          lookingForAJob: action.dataForm.lookingForAJob,
          lookingForAJobDescription: action.dataForm.lookingForAJobDescription,
        },
      };
    }
    default: {
      // alert(`Error: is  not to type: ${action.type} (profileReducer)`);
      return state;
    }
  }
};

export const addPostActionCreator = (newMyPost) => ({
  type: ADD_POST,
  newMyPost,
});
export const updateNewPostTextActionCreator = (text) => ({
  type: UPDATE_NEW_POST_TEXT,
  newText: text,
});
export const increaseLikeActonCreator = (id) => ({
  type: INCREASE_LIKE,
  id: id,
});
export const setUserProfile = (profile) => ({
  type: SET_USER_PROFILE,
  profile,
});

export const setStatus = (status) => ({
  type: SET_USER_STATUS,
  status: status,
});

export const getProfile = (profileId) => {
  return async (dispatch) => {
    const data = await userAPI.getProfile(profileId);
    dispatch(setUserProfile(data));
  };
};

export const getUserStatus = (id) => {
  return async (dispatch) => {
    const status = await profileAPI.getStatus(id);
    dispatch(setStatus(status.data));
  };
};

export const updateStatus = (status) => {
  return async (dispatch) => {
    const response = await profileAPI.updateStatus(status);
    if (response.data.resultCode === 0) {
      dispatch(setStatus(status));
    } else {
    }
  };
};

const savePhotoSuccess = (photos) => ({ type: SAVE_FOTO_SUCCESS, photos });

export const savePhoto = (file) => {
  return async (dispatch) => {
    const data = await profileAPI.saveFoto(file);
    if (data.resultCode === 0) {
      dispatch(savePhotoSuccess(data.data.photos));
    }
  };
};

export const saveProfile = (dataForm) => {
  return async (dispatch, getState) => {
    const userId = getState().auth.userId;
    const data = await profileAPI.saveProfile(dataForm);
    if (data.resultCode === 0) {
      dispatch(getProfile(userId));
      return Promise.resolve()
    } else {
      const message =
        data.messages.length > 0
          ? data.messages[0]
          : "   ";
      dispatch(
        stopSubmit("profileData", objError(message)));
      return Promise.reject("");
    }
  };
};

const objError = (messages)=>{
  let kyeError = "";
  let textKey;
  let lowSumbol;
  let errorObject;
if (messages[0] === "T"){
  textKey = messages.split(" ")[5].split("");
  textKey.pop();
  textKey.shift();
  lowSumbol = textKey[0].toLowerCase();
  textKey.shift();
  textKey.unshift(lowSumbol);
  kyeError = textKey.join("");
  errorObject = [[kyeError, messages]];
  let ob = Object.fromEntries(errorObject);
  return ob;
}else{
  textKey = messages.split("->")[1].split("");
  textKey.pop();
  lowSumbol = textKey[0].toLowerCase();
  textKey.shift();
  textKey.unshift(lowSumbol);
  kyeError = textKey.join("");
  errorObject = [[kyeError, messages]];
  let ob = Object.fromEntries(errorObject);
  return {
    contacts: ob
  }
}
}


export const setSaveProfile = (dataForm) => ({ type: SAVE_PROFILE, dataForm });

export const deletePost = (postId) => ({ type: DELETE_POST, postId });

export default profileReducer;
