import React from 'react';

import classesCartUser from './CartUser.module.css'

import Preloader from '../../FindUsers/UsersAPIComponent/Preloader/Preloader';
import avaDefaultUser from '../../../assets/images/images.png';
import ProfileStatus from './ProfileStatus/ProfileStatus';

import ProfileStatusWithHooks from "./ProfileStatus/ProfileStatusWithHooks";
import { useState } from 'react';
import ProfileDataForm from "./ProfileDataForm/ProfileDataForm"

import { Navigate } from 'react-router-dom'


const LoadFotoMyProfile = (props) => {
  const onMainPhototSelected = (event) => {
    if (event.target.files.length) {
      props.savePhoto(event.target.files[0]);
    }
  }
  return (<div className={classesCartUser.cart__img} >
    <img className={classesCartUser.cart__img} src={props.profile.photos.small === null ? avaDefaultUser : props.profile.photos.large}></img>
    {props.isOwner ?
      <input className={classesCartUser.addedMyFoto} type='file' onChange={onMainPhototSelected} /> :
      null}
  </div>)
};

const CartUser = (props) => {
  const [editMode, setEditMode] = useState(false);



  const initialValuesForm = {
    fullName: "Lexa",
    lookingForAJob: true,
    lookingForAJobDescription: "I work in ru-drive",
    aboutMe: "My hobbu is hockei",
  };




  const goToEditMode = () => {
    setEditMode(true);
  }
  const endEditMode = () => {
    setEditMode(false);
  }

  const onSubmit = (dataForm) => {

    props.saveProfile(dataForm).then(
      ()=>{
        endEditMode();
      }).catch(()=>{})
  }

  if (!props.profile) {
    return <Preloader />
  }

  return (<div>
    <div className={classesCartUser.cart}>
      <LoadFotoMyProfile
        isOwner={props.isOwner}
        profile={props.profile}
        savePhoto={props.savePhoto} />
      {/* {editMode ? <ProfileDataForm onSubmit={onSubmit} initialValues={props.profile} profile={props.profile}/> : <ProfoleData profile = {props.profile} isOwner={props.isOwner} goToEditMode = {goToEditMode}/>} */}
      {editMode ? <ProfileDataForm onSubmit={onSubmit} initialValues={initialValuesForm} profile={props.profile} /> : <ProfoleData profile={props.profile} isOwner={props.isOwner} goToEditMode={goToEditMode} />}
      <div className={classesCartUser.status}>
        :
        <ProfileStatusWithHooks status={props.status} updateStatus={props.updateStatus} />
      </div>
    </div>
  </div>
  );
};

const ProfoleData = (props) => {
  return (<div className={classesCartUser.cart__content}>
    <div className={classesCartUser.Name}> {props.profile.fullName}</div>
    <div className={classesCartUser.dataBritch}>  : {props.profile.lookingForAJob ? "" : ""}</div>
    <div className={classesCartUser.City}>  : {props.profile.lookingForAJobDescription}</div>
    <div className={classesCartUser.Education}>  : {props.profile.aboutMe}</div>
    <div className={classesCartUser.WebSite}> :
      {Object.keys(props.profile.contacts).map((key, index) => <Contact key={index} contactTitle={key} contactValue={props.profile.contacts[key]} />)}
      {props.isOwner ?
        <div> <button onClick={props.goToEditMode}> </button></div> :
        null}
    </div>
  </div>)
}


export const Contact = ({ contactTitle, contactValue }) => {
  return (<div className={classesCartUser.WebSite}>
    {contactTitle}: {contactValue == null ? " =((" : contactValue}
  </div>)
}

export default CartUser;

